@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-image-grid{
	background: #fff;
	position: relative;

	&:before{
		padding-top: 80%;
		display: block;
		content: '';
	}

	.grid{
		.absolute(0,_,_,0);
		.size(100%);

		.item{
			display: block;
		}

		&-left{
			float: left;
			.size(34%, 100%);

			.item{
				height:34%;
				.relative(_,_,0,0);

				img{
					.absolute(_,_,0,0);
				}
			}

			.item-big{
				height:66%;
			}
		}

		&-right{
			float: left;
			.size(66%, 100%);

			.item{
				float: left;
				.size(50%, 33%);

				.relative(_,_,0,0);

				img{
					.absolute(_,_,0,0);
				}
			}

			.item-bottom{
				height: 34%;
			}
		}
	}

	.bd-left{
		border-left: 1px solid @border-color-base;
	}

	.bd-right{
		border-right: 1px solid @border-color-base;
	}

	.bd-top{
		border-top: 1px solid @border-color-base;
	}

	.bd-bottom{
		border-bottom: 1px solid @border-color-base;
	}
}