@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-tabs{
	width:100%;
	display: table;
	@active-color:#aa3878;

	.item{
		float: left;
		width:50%;
		background: #f9f9f9;
		padding:3px 10px 4px 10px;
		color:#999;
		text-align: center;

		p{
			.ellipsis();

			&.time{
				font-size: @font-size-lg;
			}
			&.over{
				font-size: @font-size-sm;
				.count-down{
					padding-left:5px;
				}
			}
		}

		&.active-item{
			background: @active-color;
			color:#fff;
			position: relative;


			&:after{
				content:'';
				.absolute(_,_,-10px,50%);
				.size(0);
				line-height: 0;
				border-style: solid dashed dashed dashed;
				border-color: @active-color transparent transparent transparent;
				border-width:5px;
			}
		}
	}
}


.fn-@{cms-ns}-time-limit{
	.pro-wrap{
		background-color: #fff;
		padding-left: 10px;
	}
	.proline{
		@item-size: 110;

		padding-top: 10px;
		width: 100%;
		overflow: hidden;

		.pro-img{
			float: left;
			.size(unit(@item-size, px));
		}

		.info{
			margin-left: unit(@item-size + 15, px);
			height: unit(@item-size + 15, px);
			padding-right: 10px;
			border-bottom: 1px solid @border-color-base;

			&-inner{
				position: relative;
				height: 100%;
			}
		}

		.title{
			display: block;
			line-height: 18px;
			height:36px;
			overflow: hidden;
		}
		.address{

			margin:10px 0;
			.ellipsis();
			.img-flag{
				.size(22px,14px);
				margin-top:3px;
				max-width:100%;
				display: inline-block;
				vertical-align: top;
			}
			.name{
				color:#666;
				display: inline-block;
				vertical-align: top;
			}
		}
		.price{
			.absolute(_,_,10%,0);
			width:100%;
			.fn-price{
				padding-right:3px;
			}
			del{
				font-size: @font-size-sm;
				color:#999;
				text-decoration: line-through;
			}
		}


		.buy-btn{
			.absolute(_,0,10%,_);
			padding: 5px;
			background: @ht-color;
			color:#fff;
			text-align: center;
		}
	}
}