@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-subject{
	background: #fff;

	.item{

		position: relative;
		margin-bottom:1px;
		overflow:hidden;
		&:before{
			content:'';
			display: block;
			padding-top:29.3%;
		}

		a{
			display: block;
			.absolute(0,_,_,0);
			.size(100%);
		}

		&-icon{
			.absolute(0,3.2%,_,_);
			.size(13.3%,54.5%);

			&:before{
				content: "";
				display: block;
				width: 100%;
				padding-top: 100%;
			}
			.item-icon-img{
				.absolute(0,_,_,0);
				z-index:2;
				.size(100%);
			}
		}
	}
}