@import "../../_plugin/cmp-common-title.less";

@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-hd{
	.cmp-common-hd(@world-color);

	.hd-limit-time{
		width:50%;
		float: left;
		color:#333;
		text-align: left;

		.word{
			padding-right:10px;
		}
		.num{
			background: #000;
			color:#fff;
			padding:1%;
			border-radius: 3px;
		}
	}
	.hd-more{
		float: right;
		width:20%;
		text-align: right;

		&-icon{
			display: inline-block;
			background: @world-color;
			width:20px;
			border-radius: 3px;
			color:#fff;
			position: relative;
			margin-left:8%;
	
			&:before,&:after{
				content: '';
				.absolute(40%,_,_,_);
				.size(30%,5%);
				background-color: #fff;
				transform-origin: 50% 50%;
			}
	
			&:before{
			    left: 35%;
				transform: rotate(45deg);
			}
			&:after{
			    right: 35%;
			    top:60%;
				transform: rotate(-45deg);
			}
		}
	}
}

