@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-nation{
	background: #fff;

	.line{
		position: relative;
		border-bottom: 1px #fff solid;

		&:before{
			content:'';
			display: block;
			padding-top:35.3%;
		}

		&-wrap{
			.absolute(0,_,_,0);
			.size(100%);
		}

		&:last-child{
			border-bottom: none;
		}
	}

	.item{
		float: left;
		.size(33%,100%);

		.relative(_,_,0,0);

		img{
			.absolute(_,_,0,0);
		}

		&:nth-of-type(2){
			width:34%;
		}
	}

	.bd-left{
		border-left:1px #fff solid;
	}
	.bd-right{
		border-right:1px #fff solid;
	}

}