@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-brand-grid{
	background: #fff;

	.grid{
		position: relative;
		border-bottom: 1px solid @border-color-base;

		&:before{
			padding-top: 34.4%;
			display: block;
			content: '';
		}

		.item-list{
			.absolute(0,_,_,0);
			.size(100%);
		}

		.item{
			float: left;
			.size(25%, 100%);
			text-align: center;

			position: relative;

			p{
				.ellipsis();
				text-align: center;
			}
			.tlt{
				font-size: @font-size-base;
				font-weight: 400;
				padding:8% 1% 0px 1%;
				color:@text-color;
			}
			.f-tlt{
				color:#999;
				font-size: @font-size-sm;
				padding:0 1%;
			}

			.item-img{
				.absolute(_,_,2%,50%);
				width:85%;
				margin-left:-42.5%;
			}
			&-big{
				width: 50%;

				.tlt{
					text-align: left;
					padding-right:3%;
					padding-left:3%;
				}
				.t-line{
					text-align: left;
					margin-right:55%;
					padding-left:3%;

				}
				.tlt{
					padding-top:4%;
				}
				.item-img{
					.absolute(_,_,2%,_);
					width:53.5%;
					margin-left:-5%;
				}

				.icon{
					display:block;
					margin-left:3%;
					margin-top:1%;
					.size(9.2%,8.8%);
				}

			}
		}

		&:last-child{
			border-bottom: none;
		}
	}

	.bd-left{
		border-left: 1px solid @border-color-base;
	}

	.bd-right{
		border-right: 1px solid @border-color-base;
	}

	.bd-top{
		border-top: 1px solid @border-color-base;
	}

	.bd-bottom{
		border-bottom: 1px solid @border-color-base;
	}
}

@media (min-width: @screen-md-min) {
	.fn-cms-brand-grid .grid .item{
		.tlt{
			font-size: @font-size-xl;
		}
		.f-tlt{
			font-size: @font-size-lg;	
		}	
	} 
}