@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-chosen{
	background: #fff;
	ul{
		border-top:1px @border-color-base solid;
	}
	.item{
		padding-top:10px;
		border-top:1px @border-color-base solid;
		margin-left:3.2%;
		height:130px;
		&:first-child{ border-top:0 none;}
	}
	.pic{
		.size(120px);
		float: left;
	}
	.info{
		margin-left:130px;
		padding-right:10px;
	}
	.address{
		color: #999;
		margin-top:3px;
		.ellipsis();

		img{
			.inline-block();
			float: left;
			.size(22px,14px);
			margin:4px 10px 0 0;
		}
		.country{
			font-size: @font-size-sm;
			line-height:14px;
		}
	}
	.tlt{
		line-height:1.4;
		height: unit(1.4 * @font-size-base * 2 , rem);
		font-size: @font-size-base;
		overflow: hidden;
		color:#333;
		font-weight: 500;
		.margin(6px,0);
	}
	.price{

		font-size: @font-size-lg;
		vertical-align: middle;
		color:#999;
		.now{
			margin-right:5px;
			font-weight: 400;
		}
		del{
			color: #999;
			font-size:@font-size-sm;
			padding-left:5px;
		}
		.discount{
			line-height:1;
			.inline-block();
			background: #db384c;
			border-radius: 5px;
			.relative(0,_,_,0;);
			color:#fff;
			font-size: @font-size-base;
			padding:1px 3px;

			b{line-height:1.4;}
		}
	}
	.unable-sales{

		position: relative;
		img{
			-webkit-filter: grayscale(100%);
			-moz-filter: grayscale(100%);
			-ms-filter: grayscale(100%);
			-o-filter: grayscale(100%);
			filter: grayscale(100%);
			filter: gray;
		}
		&:before{
			content:'';
			.absolute(20%,_,_,20%);
			z-index:99;
			display: block;
			.size(60%);
			border-radius: 60%;
			background: data-uri('@{cms-image-dir}/world-no-sale.png') no-repeat 0 0;
			background-size: 100% 100%;
		}
	}
}
