@import "../_world-variables.less";
@import "../_world-mixins.less";

.fn-@{cms-ns}-world-nav{
	
	

	.nav{
		background: #F9F9F9;
		padding: 5px 0 10px 0;

		.swiper-slide{
			width: 16.66%;
			display: block;
			text-align: center;

			> span{
				font-size: @font-size-sm;
				display: block;


				span{
					letter-spacing: 2px;
					position: relative;
				}
			}

			> i{
				font-size: @font-size-sm * 2;
				display: block;
				line-height: 1.4;
			}
		}

		.active{
			color: #AA3878;

			> span span{
				&:before{
					content: '';
					.absolute(110%,_,_,0);
					background: #AA3878;
					.size(100%, 2px);
				}
			}
		}
	}
}

