@import "../_cms.less";
@import "./_world-variables.less";
@import "./_world-mixins.less";

.iconfont-face(@world-icon-name, @world-icon-url);

.@{world-icon-name}{
	.iconfont-mixin(@world-icon-name);
}

.@{container-class}{

	.unable-sales{
			position: relative;
			img{
				-webkit-filter: grayscale(100%);
				-moz-filter: grayscale(100%);
				-ms-filter: grayscale(100%);
				-o-filter: grayscale(100%);
				filter: grayscale(100%);
				filter: gray;
			}
			&:after{
				content:'';
				position: absolute;
				left: 20%;
				top:20%;
				z-index:99;
				display: block;
				.size(60%);
				border-raduis: 50%;
				background: data-uri('@{cms-world-image-path}/world-no-sale.png') no-repeat 0 0;
				background-size: 100% 100%;
			}
		}
}
